@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  src: url("../../src/fonts/Poppins/Poppins-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  src: url("../../src/fonts/Poppins/Poppins-Bold.ttf") format("truetype");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 900;
  src: url("../../src/fonts/Poppins/Poppins-Black.ttf") format("truetype");
}
@font-face {
  font-family: "Playfair Display";
  font-style: normal;
  font-weight: 400;
  src: url("../../src/fonts/Playfair_Display/PlayfairDisplay-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Playfair Display";
  font-style: normal;
  font-weight: 700;
  src: url("../../src/fonts/Playfair_Display/PlayfairDisplay-Bold.ttf") format("truetype");
}
@font-face {
  font-family: "Playfair Display";
  font-style: normal;
  font-weight: 900;
  src: url("../../src/fonts/Playfair_Display/PlayfairDisplay-Black.ttf") format("truetype");
}
[class*=__container] {
  width: min(95%, 120rem);
  margin-inline: auto;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  min-width: 32rem;
  font: 400 1.4rem/1.5 "Poppins", sans-serif;
}
@media (min-width: 768px) {
  body {
    font: 400 1.6rem/1.5 "Poppins", sans-serif;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

h1 {
  font: 700 4rem/1.2 "Poppins", sans-serif;
}

h2 {
  font: 700 3.2rem/1.2 "Poppins", sans-serif;
}

h3 {
  font: 700 2.4rem/1.2 "Poppins", sans-serif;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
}

input,
textarea {
  outline: none;
}

.header {
  padding-block: 5rem;
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.jpg");
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .header {
    min-height: 50rem;
  }
}
@media (min-width: 1024px) {
  .header {
    min-height: 75rem;
  }
}
.header__logo {
  width: 10rem;
  height: 7rem;
  margin-inline: auto;
}
@media (min-width: 768px) {
  .header__logo {
    width: 15rem;
    height: 9rem;
    margin-inline: unset;
  }
}
.header__content {
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header__content {
    max-width: 40rem;
    margin-top: 10rem;
    text-align: left;
  }
}
@media (min-width: 1024px) {
  .header__content {
    max-width: 50rem;
    margin-top: 14rem;
  }
}
.header__titulo {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .header__titulo {
    font-size: 4.8rem;
  }
}
@media (min-width: 1024px) {
  .header__titulo {
    font-size: 6rem;
  }
}
.header__buttom {
  width: 100%;
  margin-top: 1.6rem;
  padding: 0.8rem 3.2rem;
  display: block;
  font-weight: 700;
  color: #313c4e;
  font-family: "Poppins", sans-serif;
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .header__buttom {
    width: auto;
    display: inline-block;
  }
}

@supports (background-image: url("../img/header.webp")) {
  .header {
    background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.webp");
  }
}
@supports (background-image: url("../img/header.avif")) {
  .header {
    background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.avif");
  }
}
.podcast {
  padding-block: 3rem;
}
@media screen and (min-width: 768px) {
  .podcast {
    padding-block: 8rem;
  }
}
.podcast__grid {
  display: grid;
  gap: 1.6rem;
}
@media screen and (min-width: 768px) {
  .podcast__grid {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.podcast__img {
  width: 100%;
  height: auto;
}
.podcast__titulo {
  position: relative;
  z-index: 10;
  width: fit-content;
  margin-bottom: 0.4rem;
  color: #313c4e;
}
.podcast__titulo::after {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0.4rem;
  width: 102%;
  height: 0.8rem;
  display: block;
  background-color: #84d9bc;
}

.beneficios {
  overflow-x: hidden;
  padding-block: 3rem;
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .beneficios {
    padding-block: 8rem;
  }
}
.beneficios__grid {
  display: grid;
  gap: 1.6rem;
  justify-items: center;
}
@media (min-width: 768px) {
  .beneficios__grid {
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
@media (min-width: 768px) {
  .beneficios__content {
    grid-column: 1/4;
  }
}
.beneficios__titulo {
  position: relative;
  z-index: 10;
  width: fit-content;
  margin-bottom: 0.4rem;
  color: #313c4e;
}
.beneficios__titulo::after {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0.4rem;
  width: 102%;
  height: 0.8rem;
  display: block;
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .beneficios__picture {
    grid-column: 4/6;
  }
}
.beneficios__img {
  width: 100%;
  max-width: 40rem;
  height: auto;
}
@media (min-width: 768px) {
  .beneficios__img:hover {
    transform: scale(1.1) rotate(9deg);
    transition: transform 0.3s;
  }
}

.estadisticas {
  padding-block: 3rem;
}
@media (min-width: 768px) {
  .estadisticas {
    padding-block: 8rem;
  }
}
.estadisticas__titulo {
  position: relative;
  z-index: 10;
  width: fit-content;
  margin-bottom: 0.4rem;
  color: #313c4e;
}
.estadisticas__titulo::after {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0.4rem;
  width: 102%;
  height: 0.8rem;
  display: block;
  background-color: #84d9bc;
}
.estadisticas__img {
  width: 100%;
  height: auto;
  margin-top: 2.4rem;
}

.planes {
  padding-block: 3rem;
  background-color: #313c4e;
}
@media (min-width: 768px) {
  .planes {
    padding-block: 8rem;
  }
}
.planes__titulo {
  position: relative;
  z-index: 10;
  width: fit-content;
  margin-bottom: 0.4rem;
  color: #FFFFFF;
}
.planes__titulo::after {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0.4rem;
  width: 102%;
  height: 0.8rem;
  display: block;
  background-color: #84d9bc;
}
.planes__descripcion {
  color: #FFFFFF;
}
.planes__grid {
  margin-top: 2.4rem;
  display: grid;
  gap: 1.6rem;
}
@media (min-width: 768px) {
  .planes__grid {
    margin-top: 8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: unset;
  }
}
.planes__plan {
  padding: 12rem 2.4rem 4rem;
  display: grid;
  gap: 0.8rem;
  justify-items: center;
  text-align: center;
  background-color: #FFFFFF;
  background-size: 8rem;
  background-repeat: no-repeat;
  background-position: center 2rem;
}
@media (min-width: 768px) {
  .planes__plan {
    padding: 12rem 4rem 4rem;
  }
  .planes__plan:hover {
    transform: scale(1.1);
    transition: transform 0.5s;
  }
}
.planes__plan--negocio {
  z-index: 10;
  background-color: #84d9bc;
  background-image: url("../img/icono_negocio.svg");
}
@media (min-width: 768px) {
  .planes__plan--negocio {
    padding: 16rem 4rem 8rem;
    border-radius: 0.8rem;
  }
}
.planes__plan--negocio button {
  border: 1px solid #FFFFFF;
}
.planes__plan--inicio {
  background-image: url("../img/icono_inicio.svg");
}
.planes__plan--pro {
  background-image: url("../img/icono_pro.svg");
}
.planes__precio {
  font-weight: 700;
}
.planes__lista {
  margin-block: 1.6rem;
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}
.planes__botom {
  width: 100%;
  padding: 1.2rem 3.2rem;
  border: none;
  display: block;
  font-weight: 700;
  color: #313c4e;
  background-color: #84d9bc;
}

.testimoniales {
  padding-block: 3rem;
}
@media (min-width: 768px) {
  .testimoniales {
    padding-block: 8rem;
  }
}
.testimoniales__content {
  display: grid;
  gap: 2.4rem;
  justify-items: center;
  align-items: center;
}
@media (min-width: 768px) {
  .testimoniales__content {
    grid-template-columns: 1fr 2fr;
  }
}
@media (min-width: 1024px) {
  .testimoniales__content {
    grid-template-columns: 2fr 3fr;
  }
}
.testimoniales__titulo {
  position: relative;
  z-index: 10;
  width: fit-content;
  margin-bottom: 0.4rem;
  color: #313c4e;
}
.testimoniales__titulo::after {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: 0.4rem;
  width: 102%;
  height: 0.8rem;
  display: block;
  background-color: #84d9bc;
}
.testimoniales__grid {
  margin-top: 2.4rem;
  display: grid;
  gap: 2.4rem;
}
@media (min-width: 768px) {
  .testimoniales__grid {
    grid-template-columns: repeat(9, 1fr);
    gap: 2.4rem;
  }
}
.testimoniales__testimonial {
  position: relative;
  padding: 1.6rem 1.6rem 1.6rem 4rem;
  border-radius: 1.6rem;
  border: 0.8rem solid #313c4e;
  background-color: #84d9bc;
}
.testimoniales__testimonial::before {
  content: '"';
  position: absolute;
  top: -1.2rem;
  left: 0.8rem;
  font-size: 8rem;
  color: #313c4e;
  opacity: 0.5;
}
@media (min-width: 768px) {
  .testimoniales__testimonial:nth-child(1) {
    grid-column: 1/8;
    grid-row: 1/2;
  }
  .testimoniales__testimonial:nth-child(2) {
    grid-column: 2/9;
    grid-row: 2/3;
  }
  .testimoniales__testimonial:nth-child(3) {
    grid-column: 3/10;
    grid-row: 3/4;
  }
}

.footer {
  padding-top: 3rem;
  background-color: #313c4e;
}
@media (min-width: 768px) {
  .footer {
    padding-top: 8rem;
  }
}
.footer__img {
  width: 20rem;
  height: 10rem;
  margin-inline: auto;
  margin-bottom: 2.4rem;
}
.footer__grid {
  display: grid;
  gap: 2.4rem;
}
@media (min-width: 768px) {
  .footer__grid {
    grid-template-columns: repeat(4, 1fr);
    align-items: center;
  }
}
.footer__titulo {
  margin-bottom: 2.4rem;
  color: #84d9bc;
}
.footer__nav {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}
.footer__link {
  color: #FFFFFF;
}
.footer__copy {
  margin-top: 2.4rem;
  padding-block: 0.8rem;
  border-top: 1px solid #84d9bc;
  text-transform: uppercase;
  text-align: center;
  color: #FFFFFF;
}